<template>
  <div id="download">
<!--    <transition>-->
<!--      <dv-loading v-if="loading">加载报告...</dv-loading>-->
<!--    </transition>-->
    <div class="load1" v-if="loading">
      <div class="rect1"></div>
      <div class="rect2"></div>
      <div class="rect3"></div>
      <div class="rect4"></div>
      <div class="rect5"></div>
      <p class="text">正在获取报告...</p>
    </div>
    <div class="loadOver" v-else>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <p class="text">下载完成</p>
    </div>
  </div>
</template>

<script>
import reportApi from "@/api/reportApi";

export default {
  name: "downloadReport",
  data() {
    return {
      loading: true,
      reportURL: '',
      from: '',
      to: '',
      url: '',
      token: ''
    }
  },
  mounted() {
    console.log("get download url")
    this.from = Number(this.$route.query.from)
    this.to = Number(this.$route.query.to)
    this.url = this.$route.query.url
    this.token = this.$route.query.token
    this.downloadReport()
  },
  methods: {
    async downloadReport(){
      this.loading = true
      if (this.reportURL === '') {
        // 获取报告链接
        await this.getReportUrl(this.from, this.to)
      }
      const a = document.createElement('a') // 创建a标签
      let docName = this.reportURL.split('/')
      docName = docName[docName.length-1]
      a.setAttribute('download', docName) // download属性
      a.setAttribute('href', this.reportURL) // href链接
      console.log("get download url, successfully")
      a.click()
      this.loading = false
    },
    async getReportUrl(from, to) {
      reportApi.setRequestUrl(this.url)
      reportApi.setToken(this.token)
      let reportMap = await reportApi.requestDocUrl(from, to)
      let reportPath = reportMap.report
      reportPath = reportPath.replace('/data/aizoo-slurm/project/aizoo-release/data-report-designer/', 'http://10.102.33.49:9095/')
      this.reportURL = reportPath
    }
  }
}
</script>

<style scoped>
#download {
  width: 100%;
  height: 100%;
  padding: 16px 16px 0 16px;
  background-color: #222326;
  position: relative;
}
.load1 {
  margin: 100px auto;
  width: 120px;
  height: 80px;
  text-align: center;
  /*font-size: 20px;*/
}
.load1 > div {
  background-color: #0AB2EF;
  height: 100%;
  width: 12px;
  margin: 1px;
  display: inline-block;
  -webkit-animation: stretchdelay 1.2s infinite ease-in-out;
  animation: stretchdelay 1.2s infinite ease-in-out;
}
.load1 .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.load1 .rect3 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
.load1 .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
.load1 .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
@-webkit-keyframes stretchdelay {
  0%, 40%, 100% { -webkit-transform: scaleY(0.4) }
  20% { -webkit-transform: scaleY(1.0) }
}
@keyframes stretchdelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }  20% {
       transform: scaleY(1.0);
       -webkit-transform: scaleY(1.0);
     }
}
.text {
  font-size: 15px;
  color: #787676;
}
.loadOver {
  margin: 100px auto;
  width: 120px;
  height: 80px;
  text-align: center;
  /*font-size: 20px;*/
}
.loadOver > div {
  background-color: #0AB2EF;
  height: 40%;
  width: 12px;
  margin: 1px;
  display: inline-block;
}
</style>