import http from './http'
import dateFormat from "dateformat"

let url = ''
let token = ''

const reportApi = {
    requestDocUrl(from, to) {
        if (!url) return
        let format = 'yyyy-mm-dd HH:MM:00'
        let start_time_dateFormat = dateFormat(from, format).replace(" ","T")
        let end_time_dateFormat = dateFormat(to, format).replace(" ","T")
        return http.post(url, {
            start_time: start_time_dateFormat,
            end_time: end_time_dateFormat,
            token
        })
    },
    setRequestUrl(newUrl) {
        url = 'http://' + newUrl
    },
    setToken(newToken) {
        token = newToken
    }
}

export default reportApi